import React from "react";
import PropTypes from "prop-types";

// Component Emoji nhn 'name' l mt props  render emoji tng ng
const Emoji = ({ name }) => {
  // Danh sch emoji tng ng vi tn
  const emojiList = {
    smile: "",
    sad: "",
    laugh: "",
    angry: "",
    love: "",
    surprised: "",
    dead: "",
    crying: "",
    party: "",
    rocket: "",
    thumbs_up: "",
    heart: "",
  };

  // Kim tra nu emoji tn ti trong danh sch
  const emoji = emojiList[name] || ""; // Nu khng tm thy, tr v du hi 

  return (
    <span role="img" aria-label={name} aria-hidden={emoji === ""}>
      {emoji}
    </span>
  );
};

// PropTypes  kim tra kiu d liu cho props
Emoji.propTypes = {
  name: PropTypes.string.isRequired,
};

export default Emoji;
